import { messageStore } from "ui/stores/messageStore";
//import { createStandaloneToast } from "@chakra-ui/toast";
import { GeneralMsgTyps } from "../common";
import { createStandaloneToast } from "@chakra-ui/react";
export function showMessage(msg) {
    messageStore.getState().add(msg);
    if (msg.noToast) {
        const { toast } = createStandaloneToast();
        if (msg.type !== GeneralMsgTyps.debug && msg.type !== GeneralMsgTyps.log) {
            let duration = 1500;
            switch (msg.type) {
                case GeneralMsgTyps.error:
                    //case GeneralMsgTyps.exception:
                    duration = 2000;
                    break;
                default:
                    break;
            }
            toast({
                title: "" + msg.title,
                description: "" + msg.message,
                status: msg.type,
                duration: duration,
                isClosable: true,
                position: "bottom-left",
            });
        }
    }
}
export function addMessageError(title, message, noToast = false) {
    showMessage({
        title: title,
        type: GeneralMsgTyps.error,
        message: message,
        noToast: noToast,
    });
    console.log("addMessageError", message);
    //    if (!this.errors) this.errors = [];
    //  this.errors.push(error);
}
